  background-color: rgb(0, 219, 139);
}
input:focus + .slider[data-v-ee813b36] {
  box-shadow: 0 0 1px rgb(0, 219, 139);
}
input:checked + .slider[data-v-ee813b36]:before {
  -webkit-transform: translateX(28px);
  transform: translateX(28px);
}
