
.information-box-container {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.information-box-container .information-picture-box {
    width: 300px;
    height: 300px;
    margin: 100px auto 0 auto;
    background-color: #000;
    border: 10px solid #fff;
    box-shadow: 0 0 10px #000;
}
.information-box-container .information-picture-box img {
      width: 100%;
}
.information-box-container .information-introduction {
    width: 320px;
    cursor: default;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.information-box-container .information-introduction .hide-info {
      width: 72px;
      height: 72px;
      position: absolute;
      right: 0px;
      border-radius: 50%;
      cursor: pointer;
}
.information-box-container .information-introduction .hide-info:hover {
        color: #008f5b;
        background-color: #3fff7a;
}
.information-box-container .information-introduction h1 {
      font-size: 2.4rem;
}
.information-box-container .information-introduction p {
      font-size: 1.6rem;
}
.information-box-container .info-btn-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 100%;
    margin: 250px 0 0 0;
}
.information-box-container .info-btn-container a {
      bottom: 0px;
      width: 60px;
      height: 60px;
      cursor: pointer;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 60% 60%;
}
.information-box-container .info-btn-container .go-home-btn {
      background-image: url("../assets/images/home.png");
}
.information-box-container .info-btn-container .go-github-btn {
      background-image: url("../assets/images/github.png");
}
.information-box-container .info-btn-container .go-follow-btn {
      background-image: url("../assets/images/follow.png");
}



