
.navigation-column {
  width: 3%;
  min-width: 60px;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: #00db8b;
  position: fixed;
  left: 0;
  top: 0;
  margin: 0;
  z-index: 4;
  box-shadow: 10px 0 20px #000;
}
.navigation-column div {
    width: 60px;
    height: 100px;
    margin: 0;
    font-size: 1.6rem;
    font-weight: bold;
    color: #fff;
    cursor: pointer;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.navigation-column div:hover {
      color: #008f5b;
      background-color: #3fff7a;
}



