
@charset "UTF-8";
html {
  font-size: 62.5%;
  width: 100%;
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Microsoft Yahei';
}
#app, .container {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  min-width: 1200px;
}
.child-view {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.button, .action-button {
  width: 120px;
  height: 40px;
  border-radius: 5px;
  border: 2px outset #008f5b;
  background-color: #00db8b;
  font-size: 1.5rem;
  font-weight: bold;
  outline: none;
  box-sizing: border-box;
}
.button:active, .action-button:active {
    background-color: #008f5b;
}
.text-view-container {
  width: 1000px;
  height: 600px;
}
.text-view-container .head-picure-introduction-view {
    width: 1000px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.text-view-container .head-picure-introduction-view > div {
      margin: 0 40px;
}
.text-view-container .head-picure-introduction-view .head-picture-box {
      position: relative;
      width: 200px;
      height: 200px;
      box-sizing: border-box;
      background-color: #000;
      border: 10px solid #fff;
      cursor: pointer;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
}
.text-view-container .head-picure-introduction-view .head-picture-box img {
        width: 100%;
}
.text-view-container .head-picure-introduction-view .head-picture-box .file-upload {
        position: absolute;
        bottom: 0;
        right: 0;
}
.text-view-container .head-picure-introduction-view .textarea {
      width: 640px;
      height: 200px;
      box-sizing: border-box;
      border-radius: 5px;
      outline: none;
      font-size: 2rem;
      font-family: "微软雅黑";
      padding: 10px;
      resize: none;
      border: 2px inset;
}
.information-setting-view {
  margin: 40px;
}
.information-setting-view .a-line-display {
    height: 30px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    margin-bottom: 30px;
}
.information-setting-view .a-line-display div {
      width: 200px;
      height: 30px;
      margin: 0;
      line-height: 1.5;
}
.information-setting-view .a-line-display .text-box {
      width: 800px;
      height: 30px;
      font-size: 2rem;
      text-indent: 5px;
      box-sizing: border-box;
      border-radius: 5px;
      outline: none;
}
.information-setting-view div > p {
  font-size: 2rem;
  color: #fff;
  /*display: inline;*/
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  margin: 0;
}
.action-button {
  margin: 20px 20px 20px 0;
}



