
html {
  font-size: 62.5%;
  width: 100%;
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Microsoft Yahei';
}
#app, .container, .half-view {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  min-width: 1200px;
}
.child-view {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.button {
  width: 120px;
  height: 40px;
  border-radius: 5px;
  border: 2px outset #008f5b;
  background-color: #00db8b;
  font-size: 1.5rem;
  font-weight: bold;
  outline: none;
  box-sizing: border-box;
}
.button:active {
    background-color: #008f5b;
}
@-webkit-keyframes alphaChange {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
.half-view {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.sign-container, .sign-up-container, .sign-in-container {
  width: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.sign-container .text-container, .sign-up-container .text-container, .sign-in-container .text-container {
    text-align: center;
    margin: 30% 20%;
    -webkit-animation: alphaChange 2.5s;
}
.sign-container .text-container h1, .sign-up-container .text-container h1, .sign-in-container .text-container h1 {
      font-size: 40px;
      cursor: default;
}
.sign-container .text-container p, .sign-up-container .text-container p, .sign-in-container .text-container p {
      font-size: 25px;
      cursor: default;
}
.sign-up-container {
  background-color: #00db8b;
}
.sign-up-container h1 {
    color: #000000;
    font-size: 40px;
    cursor: default;
}
.sign-up-container p {
    color: #000000;
    font-size: 25px;
    cursor: default;
}
.sign-in-container {
  background-color: #161616;
}
.sign-in-container h1 {
    color: #ffffff;
    font-size: 40px;
    cursor: default;
}
.sign-in-container p {
    color: #ffffff;
    font-size: 25px;
    cursor: default;
}
.form-container, .sign-up-form-container, .sign-in-form-container {
  text-align: center;
  width: 400px;
  -webkit-animation: alphaChange 2.5s;
}
.form-container .text-box, .sign-up-form-container .text-box, .sign-in-form-container .text-box {
    width: 300px;
    height: 50px;
    font-size: 20px;
    border: 0;
    border-radius: 5px;
    outline: none;
    text-indent: 10px;
}
.form-container p, .sign-up-form-container p, .sign-in-form-container p {
    font-size: 20px;
    cursor: default;
}
.form-container .sign-button, .sign-up-form-container .sign-button, .sign-in-form-container .sign-button {
    width: 300px;
    height: 50px;
    margin-top: 50px;
    border-radius: 5px;
    border: 0;
    outline: none;
    font-size: 25px;
}
.sign-up-form-container p {
  color: #000000;
}
.sign-up-form-container .sign-button {
  background-color: #161616;
  color: #ffffff;
}
.sign-up-form-container .sign-button:active {
    background-color: #000000;
}
.sign-in-form-container p {
  color: #ffffff;
}
.sign-in-form-container .sign-button {
  background-color: #00db8b;
  color: #000000;
}
.sign-in-form-container .sign-button:active {
    background-color: #008f5b;
}
.change-button, .change-button-sign-in, .change-button-sign-up {
  text-align: center;
  line-height: 75px;
  width: 150px;
  height: 150px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: absolute;
  margin: auto;
  -webkit-transform: rotate(45deg);
  overflow: hidden;
  cursor: pointer;
}
.change-button p, .change-button-sign-in p, .change-button-sign-up p {
    font-size: 35px;
    color: #ffffff;
    -webkit-transform: rotate(-45deg);
    -webkit-animation: alphaChange 2.5s;
}
.change-button-sign-in {
  background-color: #00db8b;
}
.change-button-sign-up {
  background-color: #161616;
}



